.post {
    display: flex;
    align-items: flex-start;
    border-bottom: 1px solid #e6ecf0;
    padding-bottom: 10px;
}

.post__body {
    flex: 1;
    padding: 10px;
}

.post__body>img {
    width: 100%;
    border-radius: 20px;
}

.post__footer {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.post__badge {
    font-size: 14px !important;
    color: #50b7f5;
}

.post__headerSpecial {
    font-weight: 400;
    font-size: 12px;
    color: grey;
}

.post__avatar {
    padding: 20px;
}

.post__headerDescription {
    margin-bottom: 10px;
    font-size: 15px;
}